import { Component, OnInit } from '@angular/core';
import { BankService, AuthenticationService, EthereumService } from '@app/_services';
import { Router } from '@angular/router';
import { User, BankRequest } from '@app/_models';
import { first} from 'rxjs/operators';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-list-request',
  templateUrl: './list-request.component.html',
  styleUrls: ['./list-request.component.css']
})
export class ListRequestComponent implements OnInit {
  currentUser: User;
  bankRequestLists: Array<BankRequest>;
  wallet_address;
  
  constructor(
    private spinner: NgxSpinnerService,
  	private bankService: BankService, 
  	public router: Router, 
  	private authenticationService: AuthenticationService,
  	private ethereumService: EthereumService
  ) { 
  	this.currentUser = this.authenticationService.currentUserValue;
    if(this.currentUser.role_id == 2){
      this.wallet_address = this.currentUser.bank.wallet_address;
    } else{
      this.wallet_address = null;
    }
  }

  deleteBankRequest(username, data, wallet_address){
  	var con = confirm("Are you sure, want to delete bank kyc request?");
  	if(con){
  		this.ethereumService.removeRequest(username, data, wallet_address).then((result)=>{
           this.bankService.deleteBankRequest(username, data, wallet_address).pipe(first()).subscribe((res:any)=>{
              alert("Bank kyc request deleted successfully");
              this.ngOnInit(); 
           },
           error=>{

              alert("Something went wrong please try again!"); 
           });
          
	  	}).catch((error) =>{
	  	  console.log("remove bankrquest error", error);
	      alert("Something went wrong please try again!");
	  	});
  	}
  }

  ngOnInit(){
    this.spinner.show();
  	this.bankService.listBankRequest(this.wallet_address).pipe(first()).subscribe((res:any)=>{
       console.log("result", res);
       if(res.success == 1){
       	 this.bankRequestLists = res.data;
       }else{
       	 this.bankRequestLists = null;
       }
       this.spinner.hide();
  	},
  	error=>{
      console.log("error", error);
  	});
  }

}
