import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { BankService, AuthenticationService, EthereumService } from '@app/_services';
import { Router } from '@angular/router';
import { User, Bank } from '@app/_models';
import { first} from 'rxjs/operators';

@Component({
  selector: 'app-add-request',
  templateUrl: './add-request.component.html',
  styleUrls: ['./add-request.component.css']
})
export class AddRequestComponent implements OnInit {
  bankRequestForm: FormGroup;	
  currentUser: User;

  constructor(
  	private fb: FormBuilder, 
  	private bankService: BankService, 
  	public router: Router, 
  	private authenticationService: AuthenticationService,
  	private ethereumService: EthereumService
  	) { 
  	this.bankForm();
  	this.currentUser = this.authenticationService.currentUserValue;
    
    console.log(this.currentUser.role_id);
    if(this.currentUser.role_id == 1){
      this.router.navigate(['home']);
    }
  }

  bankForm(){
    this.bankRequestForm = this.fb.group({
        name: ['', Validators.required],	
        adharAddress: ['', Validators.required],
    });
  }

  addBankRequest(name, adhareAddress){
    this.ethereumService.addRequest(name, adhareAddress, this.currentUser.bank.wallet_address).then(async(result)=>{
      let rating = await this.ethereumService.getBankRating(this.currentUser.bank.wallet_address);
      let isallowed = rating > 50;
      this.bankService.addBankRequest(name, adhareAddress, this.currentUser.bank.wallet_address, isallowed)
      .pipe(first()).subscribe((res:any) => {
        console.log(res);
        alert("Request KYC is done successfully.");
        this.router.navigate(['banks/requests']);
      },error=>{
        console.log("bnak request", error);
        //alert("This bank already exist");
      });
       
    }).catch((error) =>{
      console.log("error", error.message);
      alert("You have already requested for KYC");
    });
  }

  ngOnInit(): void {
  }

}
