import { Component, OnInit } from '@angular/core';
import { CustomerService, AuthenticationService, EthereumService } from '@app/_services';
import { ActivatedRoute, Router } from '@angular/router';
import { User, Customer } from '@app/_models';

@Component({
  selector: 'app-view-customer',
  templateUrl: './view-customer.component.html',
  styleUrls: ['./view-customer.component.css']
})
export class ViewCustomerComponent implements OnInit {
  currentUser: User;
  customerData = null;
  
  constructor(
  private customerService: CustomerService,
  private route: ActivatedRoute, 
  public router: Router, 
  private authenticationService: AuthenticationService,
  private ethereumService: EthereumService
  	) { 
  	this.currentUser = this.authenticationService.currentUserValue;
  }

  ngOnInit(){
  	this.route.params.subscribe(params => { 

    	this.ethereumService.viewCustomer(params['username'], params['password'], this.currentUser.bank.wallet_address).then((result) => {
          this.customerData = this.ethereumService.web3.utils.hexToString(result);
    	}).catch((error) => {
           console.log("Error", error);
    	});
    });

  }

}
