import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { AuthenticationService, EthereumService, CustomerService } from '@app/_services';
import { ActivatedRoute, Router } from '@angular/router';
import { User, Customer } from '@app/_models';
import { first} from 'rxjs/operators';

@Component({
  selector: 'app-edit-customer',
  templateUrl: './edit-customer.component.html',
  styleUrls: ['./edit-customer.component.css']
})
export class EditCustomerComponent implements OnInit {
  editCustomerForm: FormGroup;
  currentUser: User;
  customerData: Customer;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,  
  	public router: Router, 
  	private customerService: CustomerService,
  	private authenticationService: AuthenticationService,
  	private ethereumService: EthereumService
  	) { 
  	this.customerForm();
  	this.currentUser = this.authenticationService.currentUserValue;

  	if(this.currentUser.role_id == 1){
      this.router.navigate(['home']);
    }
  }

  customerForm(){
  	this.editCustomerForm = this.fb.group({
        name: ['', Validators.required],	
        adharAddress: ['', Validators.required],
    });
  }


  editCustomer(username, data){
    this.ethereumService.modifyCustomer(username, this.customerData.password, data, this.currentUser.bank.wallet_address).then((result)=>{
       this.customerService.editCustomer(username, data, this.currentUser.bank.wallet_address).pipe(first()).subscribe((response: any)=>{
          alert("You have modified customer data successfully");
          this.router.navigate(['customers']);
       },error=>{
          alert("Something went wrong to modify customer data");
       });
    }).catch((error)=>{
       console.log("edit error", error);
    });
  }

  ngOnInit(){
  	this.route.params.subscribe(params => {
  		this.customerService.viewCustomer(params['id'], params['username']).pipe(first()).subscribe((res:any)=>{
          if(res.success == 1){
          	this.customerData = res.data;
          	this.editCustomerForm.setValue({
          		name: res.data.username,
          		adharAddress: res.data.user_data
          	});
          }else{
          	this.customerData = null;
          }
  		},error=>{
          console.log("eroor", error);
  		});
  	});

  }

}
