import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { AuthenticationService, EthereumService, CustomerService } from '@app/_services';
import { ActivatedRoute, Router } from '@angular/router';
import { User, Customer } from '@app/_models';
import { first} from 'rxjs/operators';

@Component({
  selector: 'app-password-customer',
  templateUrl: './password-customer.component.html',
  styleUrls: ['./password-customer.component.css']
})
export class PasswordCustomerComponent implements OnInit {
  passCustomerForm: FormGroup;
  currentUser: User;
  customerData: Customer;

  constructor(
  	private fb: FormBuilder,
    private route: ActivatedRoute,  
  	public router: Router, 
  	private customerService: CustomerService,
  	private authenticationService: AuthenticationService,
  	private ethereumService: EthereumService
  	) { 
  	this.customerForm();
  	this.currentUser = this.authenticationService.currentUserValue;

  	if(this.currentUser.role_id == 1){
      this.router.navigate(['home']);
    }
  }

  customerForm(){
  	this.passCustomerForm = this.fb.group({
        name: ['', Validators.required],	
        password: ['', Validators.required],
    });
  }

 

  setPassword(username, password){
    this.ethereumService.setCustPassword(username, password, this.currentUser.bank.wallet_address).then((result)=>{
      this.customerService.setPassword(username, password, this.currentUser.bank.wallet_address).pipe(first()).subscribe((res: any) => {
       alert("You have changed password successfully");
       this.ngOnInit();
      },error=>{
        console.log(error);
      });
    }).catch((error)=>{
      alert("Something went wrong please try again in few minutes!");
    });
    
  }

  ngOnInit(){
  	this.route.params.subscribe(params => {
  		this.customerService.viewCustomer(params['id'], params['username']).pipe(first()).subscribe((res:any)=>{
          if(res.success == 1){
          	this.customerData = res.data;
          	this.passCustomerForm.setValue({
          		name: res.data.username,
          		password: res.data.password
          	});
          }else{
          	this.customerData = null;
          }
  		},error=>{
          console.log("eroor", error);
  		});
  	});
  }

}
