import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { environment } from '@environments/environment';
import { User, Customer } from '@app/_models';

@Injectable({ 
	providedIn: 'root' 
})


export class CustomerService{
   headers = new HttpHeaders().set('Content-Type', 'application/json');
   constructor(private http: HttpClient) { }

    addCustomer(username: string, data: string, wallet_address: string): Observable<Customer>{
   	  return this.http.post<Customer>(`${environment.apiUrl}/v1/customer/add`,{username: username, data: data, wallet_address: wallet_address}); 
    }

    listCustomer(wallet_address: string): Observable<Customer>{
    	let params = new HttpParams().set('wallet_address', wallet_address);
		return this.http.get<Customer>(`${environment.apiUrl}/v1/customer/list`, {params: params });
	}

	voteCustomer(username: string, rating: number, wallet_address: string): Observable<Customer>{
       return this.http.post<Customer>(`${environment.apiUrl}/v1/customer/vote`, {username: username, rating: rating, wallet_address: wallet_address});
	}

	viewCustomer(id: string, username: string): Observable<Customer>{
		let params = new HttpParams();
        params = params.append('id', id);
        params = params.append('username', username);
        return this.http.get<Customer>(`${environment.apiUrl}/v1/customer/view`,{params: params });
	}

	editCustomer(username: string, data: string, wallet_address: string): Observable<Customer>{
		return this.http.put<Customer>(`${environment.apiUrl}/v1/customer/edit`,{username: username, data: data, wallet_address: wallet_address});
	}

	deleteCustomer(username: string, wallet_address: string): Observable<Customer>{
		return this.http.post<Customer>(`${environment.apiUrl}/v1/customer/delete`, {username: username, wallet_address: wallet_address});
	}

	setPassword(username: string, password: string, wallet_address: string): Observable<Customer>{
      return this.http.put<Customer>(`${environment.apiUrl}/v1/customer/set_password`, {username: username, password: password, wallet_address: wallet_address});
	}
}