import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms'; 
import { AuthenticationService } from '@app/_services';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';
import Web3 from 'web3';

declare let window: any;
//declare let ethereum: any;

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.css']
})
export class AdminLoginComponent implements OnInit {
  web3: Web3 |  undefined = undefined;
  adminLoginForm: FormGroup;
  
  constructor(
    private fb: FormBuilder, 
    private login: AuthenticationService, 
    public router: Router
    ) { 
    this.loginForm();
    if(this.login.isLoggedIn() == true){
       this.router.navigate(['home']);
    }
  }

  loginForm(){
    this.adminLoginForm = this.fb.group({
      inputEmail: ['', [Validators.required, Validators.email]],
      inputPassword: ['', Validators.required] 
    });
  }

  adminLogin(inputEmail, inputPassword){
  	this.login.adminLogin(inputEmail, inputPassword)
    .pipe(first())
    .subscribe(data => {
      this.router.navigate(['home']);
    });
  }

  walletLogin = async () =>{
    if (!window.ethereum){
       alert("Please install MetaMask first.");
    }
      
    if (!this.web3) {
      try {
        // Request account access if needed
        await (window).ethereum.enable();

        // We don't know window.web3 version, so we use our own instance of Web3
        // with the injected provider given by MetaMask
        this.web3 = new Web3((window).ethereum);
      } catch (error) {
        window.alert('You need to allow MetaMask.');
        return;
      }
    }


    const coinbase = await this.web3.eth.getCoinbase();
    console.log("adddress", coinbase);
    if (!coinbase) {
      alert('Please activate MetaMask first.');
      return;
    }

    let nonce = this.web3.eth.getTransactionCount(coinbase);
    const signature = await this.web3.eth.personal.sign(`I am signing my one-time nonce: ${nonce}`,
        coinbase,
        '' // MetaMask will ignore the password argument here
      );

    console.log("signature", signature);

  }

  ngOnInit(): void {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('bg-dark');
  }

}
