import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { environment } from '@environments/environment';
import { Bank, User, BankRequest } from '@app/_models';

@Injectable({ 
	providedIn: 'root' 
})

export class BankService{
	headers = new HttpHeaders().set('Content-Type', 'application/json');
	constructor(private http: HttpClient) { }

	addBank(name: string, wallet_address: string, rgNumber: string, email: string, password: string): Observable<Bank>{
        return this.http.post<Bank>(`${environment.apiUrl}/v1/bank/add`, {name: name, wallet_address: wallet_address, rn: rgNumber, email: email, password: password });
	}

	listBank():Observable<Bank>{
		return this.http.get<Bank>(`${environment.apiUrl}/v1/bank/list`);
	}

	viewBank(wallet_address: string): Observable<Bank>{
		return this.http.get<Bank>(`${environment.apiUrl}/v1/bank/show/${wallet_address}`);
	}

	deleteBank(wallet_address: string): Observable<Bank>{
	   return this.http.post<Bank>(`${environment.apiUrl}/v1/bank/delete`,{wallet_address: wallet_address});	
	}

	addBankRequest(name: string, data: string, wallet_address: string, isallowed: boolean): Observable<BankRequest>{
	   return this.http.post<BankRequest>(`${environment.apiUrl}/v1/bank/request/add`, {name: name, data: data, wallet_address: wallet_address, isallowed: isallowed});
	}

	listBankRequest(wallet_address: string): Observable<BankRequest>{
	  let params = new HttpParams().set('wallet_address', wallet_address);
	  console.log("parameters", params);
	  return this.http.get<BankRequest>(`${environment.apiUrl}/v1/bank/request/list`, {params: params });
	}

	deleteBankRequest(username: string, data: string, wallet_address: string): Observable<BankRequest>{
	  return this.http.post<BankRequest>(`${environment.apiUrl}/v1/bank/request/remove`,{username: username, user_data: data, wallet_address: wallet_address});
	}
}