import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AdminLoginComponent } from './admin-login/admin-login.component';
import { AddBankComponent } from './add-bank/add-bank.component';
import { ListBankComponent } from './list-bank/list-bank.component';
import { ViewBankComponent } from './view-bank/view-bank.component';
import { AddRequestComponent } from './add-request/add-request.component';
import { ViewRequestComponent } from './view-request/view-request.component';
import { ListRequestComponent } from './list-request/list-request.component';
import { AddCustomerComponent } from './add-customer/add-customer.component';
import { ListCustomerComponent } from './list-customer/list-customer.component';
import { ViewCustomerComponent } from './view-customer/view-customer.component';
import { EditCustomerComponent } from './edit-customer/edit-customer.component';
import { PasswordCustomerComponent } from './password-customer/password-customer.component';
import { AuthGuard, RoleAuthGuard } from '@app/_helpers';


const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: AdminLoginComponent },
  { path: 'home', component: HomeComponent, canActivate: [AuthGuard] },
  { path: 'banks/add', component: AddBankComponent, canActivate: [AuthGuard, RoleAuthGuard] },
  { path: 'banks', component: ListBankComponent, canActivate: [AuthGuard] },
  { path: 'banks/view/:walletAddress', component: ViewBankComponent, canActivate: [AuthGuard] },
  { path: 'banks/requests/add', component: AddRequestComponent, canActivate: [AuthGuard] },
  { path: 'banks/requests', component: ListRequestComponent, canActivate: [AuthGuard] },
  { path: 'banks/requests/view/:walletAddress/:index', component: ViewRequestComponent, canActivate: [AuthGuard] },
  { path: 'customers/add', component: AddCustomerComponent, canActivate: [AuthGuard] },
  { path: 'customers', component: ListCustomerComponent, canActivate: [AuthGuard] },
  { path: 'customers/view/:username/:password', component: ViewCustomerComponent, canActivate: [AuthGuard] },
  { path: 'customers/edit/:id/:username', component: EditCustomerComponent, canActivate: [AuthGuard] },
  { path: 'customers/password/:id/:username', component: PasswordCustomerComponent, canActivate: [AuthGuard] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
