import { Component, OnInit } from '@angular/core';
import { CustomerService, AuthenticationService, EthereumService } from '@app/_services';
import { Router } from '@angular/router';
import { User, Customer } from '@app/_models';
import { first} from 'rxjs/operators';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-list-customer',
  templateUrl: './list-customer.component.html',
  styleUrls: ['./list-customer.component.css']
})
export class ListCustomerComponent implements OnInit {
  currentUser: User;
  customersList:Array<Customer>;
  wallet_address;
  
  constructor(
  private spinner: NgxSpinnerService,  
  private customerService: CustomerService, 
  public router: Router, 
  private authenticationService: AuthenticationService,
  private ethereumService: EthereumService
  ) { 
  	this.currentUser = this.authenticationService.currentUserValue;
    if(this.currentUser.role_id == 1){
      this.wallet_address = null;
    }else{
      this.wallet_address = this.currentUser.bank.wallet_address;
    }
  }

  
  upvoteCustomer(username){
  	this.ethereumService.upvoteCust(username,this.wallet_address).then( async(result)=>{
        let rating = await this.ethereumService.getCustRating(username, this.wallet_address);
        this.customerService.voteCustomer(username, rating, this.wallet_address).pipe(first()).subscribe((res: any)=>{
          if(res.success == 1){
            alert("You have voted successfully");
          }else{
          	console.log("error update rating in db");
            alert("Something went wrong please try again in few minutes!");
          }
          this.ngOnInit();
        },
        error=>{
          console.log(error);
        });
        this.ngOnInit();
  	}).catch((error) => {
        alert("Something went wrong please try again in few minutes!");
  	});
  }

  deleteCustomer(username){
    var del = confirm("Are you sure, want to remove this customer?");
    if(del){
      this.ethereumService.removeCustomer(username, this.wallet_address).then((result)=>{
        this.customerService.deleteCustomer(username, this.wallet_address).pipe(first()).subscribe((res:any)=>{
          alert("You have deleted customer successfully"); 
          this.ngOnInit();   
        },error=>{
          console.log(error);
        });
      }).catch((error)=>{
        alert("Something went wrong please try again in few minutes!");
      });
    } else{
      this.ngOnInit();  
    }
    
  }

  accessHistory(username){
    this.ethereumService.customerAccessHistory(username, this.wallet_address).then((result)=>{
      alert("This customer accessed by bank address "+result);
      this.ngOnInit();
    }).catch((error)=>{
      console.log("access history error", error);
    });
  }

  ngOnInit(){
    this.spinner.show();
    
  	this.customerService.listCustomer(this.wallet_address).pipe(first()).subscribe((res: any) => {
      if(res.success == 1){
      	this.customersList = res.data;
      }
      this.spinner.hide();
    },
    error=>{
       console.log(error); 
       this.customersList = null;
    });
  }

}
