import { Component, OnInit } from '@angular/core';
import { BankService, AuthenticationService, EthereumService } from '@app/_services';
import { Router } from '@angular/router';
import { User, Bank } from '@app/_models';
import { first} from 'rxjs/operators';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-list-bank',
  templateUrl: './list-bank.component.html',
  styleUrls: ['./list-bank.component.css']
})
export class ListBankComponent implements OnInit {
  currentUser: User;
  banksList:Array<Bank>;
  
  constructor(
  private spinner: NgxSpinnerService,
  private bankService: BankService, 
  public router: Router, 
  private authenticationService: AuthenticationService,
  private ethereumService: EthereumService
  ) {
    this.currentUser = this.authenticationService.currentUserValue;
    console.log(this.currentUser);
  }

  ngOnInit(){
    this.spinner.show();
    this.bankService.listBank().pipe(first()).subscribe((res: any) => {
      console.log(res);
      if(res.success == 1){
      	this.banksList = res.data;
      }
      this.spinner.hide();
    },
    error=>{
       console.log(error); 
    });
  }

  deleteBank(walletAddress){
    var result = confirm("Are you sure, want to delete bank?");
    if(result){
      this.bankService.deleteBank(walletAddress).pipe(first()).subscribe((res: any) => {
        if(res.success == 1){
          this.ngOnInit();
        }
      },
      error =>{
        alert("Something went wrong, please try again after few minutes");
      });
    }
  }


  upvoteBank(walletAddress, bankSessionWalletAddress){
     this.ethereumService.upvotebank(walletAddress, bankSessionWalletAddress).then((result) => {
        alert("You have successfully voted for this bank");
        this.ngOnInit();
     }).catch((error) => {
        console.log("error", error);
     });
  }

}
